export default {
  name: "Article",
  head() {
    return {
      title: "Kat Lightfoot Press Articles"
    };
  },
  async asyncData({ $prismic, params, error }) {
    try {
      const article = (await $prismic.api.getByUID('press', params.uid)).data

      return {
        document: article
      };
    } catch (e) {
      error({ statusCode: 404, message: "Page not found" });
    }
  }
};
